import React, { Component } from 'react';
import $ from 'jquery';

class SideMenu extends Component {
    componentDidMount() {
        $(".k-sidemenu__input").focus(function() {
            $(this).siblings(".k-sidemenu__label").addClass('active'); 
        });
        $(".k-sidemenu__input").blur(function() {
            let thisVal = $(this).val();
            if(!thisVal) {
                $(this).siblings(".k-sidemenu__label").removeClass('active');
            }
        });
    }

    closeSideMenu = () => {
        $(".k-sidemenu__wrap").fadeOut(300);
        $(".k-sidemenu").removeClass("active");
    }

    render() {
        return (
            <div>
                <section className="k-sidemenu__wrap">
                    <article className="k-dimmer" onClick={this.closeSideMenu}></article>
                    <article className="k-sidemenu">
                    <a href="javascript:void(0)" class="k-sidemenu__close txt-hidden" onClick={this.closeSideMenu}>
                         </a>
                        <form className="k-sidemenu__form clearfix">
                            <div className="sidemenu-input__box">
                                <input id="sideID" type="text" name="ID" className="k-sidemenu__input" />
                                <label for="sideID" className="k-sidemenu__label">()</label>
                            </div>
                            <div className="sidemenu-input__box">
                                <input id="sidePW" type="password" name="PW" className="k-sidemenu__input" />
                                <label for="sidePW" className="k-sidemenu__label"></label>
                            </div>
                            <button className="k-sidemenu__btn k-btn full"></button>
                            <input id="idSave" type="checkbox" className="k-check k-hidden"/>
                            <label for="idSave" className="k-check__label"> </label>
                            <nav className="k-client__nav">
                                <a href="#"></a>
                                <a href="#">ID/PW </a>
                            </nav>
                        </form>
                        <nav className="k-sidemenu__nav">
                            <a href="#"></a>
                            <a href="#"></a>
                            <a href="#"></a>
                            <a href="#"></a>
                            <a href="#">FAQ</a>
                            <a href="#"> </a>
                        </nav>
                    </article>
                </section>
            </div>
        );
    }
}

export default SideMenu;