import React, { Component } from 'react';
import $ from "jquery";

class Header extends Component {

    activeSideMenu = () => {
        $(".k-sidemenu__wrap").fadeIn(300);
        $(".k-sidemenu").addClass("active");
    }

    render() {
        return (
            <div>
                <header className="k-header">
                    <div className="k-header__banner">
                        <img src={require('../images/top-banner.jpg')} alt="4       !"/>
                    </div>
                    <nav className="k-navbar">
                        <div className="k-section">
                            <ul className="k-site__nav">
                                <li><a href="#">STAR PAY</a></li>
                                <li><a href="#">STAR KST</a></li>
                            </ul>
                            <select className="k-lang__select">
                                <option value="KOR">KOR</option>
                                <option value="ENG">ENG</option>
                                <option value="CHI">CHI</option>
                            </select>
                            <ul className="k-main__nav">
                                <li><a href="#"></a></li>
                                <li><a href="#"></a></li>
                                <li><a href="/mypage"></a></li>
                                <li><a href="javascript:void(0);" onClick={this.activeSideMenu}></a></li>
                            </ul>
                        </div>
                    </nav>
                    <div className="k-section-full k-search__wrap">
                        <section className="k-search__box k-section">
                                <h1 className="k-logo">
                                    <a href="/">
                                        <img src={require('../images/logo.png')} alt="KSTAR LOGO"/>
                                    </a>
                                </h1>
                                <form className="k-search__form">
                                    <input type="text" className="k-search__input" placeholder="   ."/>
                                    <a href="javascript:void(0)" class="k-search__btn txt-hidden"></a>
                                    <article className="k-notice__thumb">
                                        <i className="k-notice__i">Notice</i>
                                        <ul className="k-notice__list">
                                            <li><a href="#" className="k-truncate"> TEST</a></li>
                                        </ul>
                                    </article>
                                </form>
                                <a href="#" className="k-search__banner">
                                    <img src={require('../images/header-banner.jpg')} alt="STAR  !"/>
                                </a>
                        </section>
                    </div>
                </header>
            </div>
        );
    }
}

export default Header;